:root,[data-bs-theme="light"] {
    --bd-purple: rgb(76, 11, 206);
    --bd-violet: #712cf9;
    --bd-violet: rgb(44, 92, 249);
    --bd-accent: #ffe484;
    --bd-violet-rgb: 112.520718,44.062154,249.437846;
    --bd-blue-rgb: 44.520718,92.062154,249.437846;
    --bd-accent-rgb: 255,228,132;
    --bd-pink-rgb: 214,51,132;
    --bd-teal-rgb: 32,201,151;
    --bd-violet-bg: var(--bd-violet);
    --bd-toc-color: var(--bd-violet);
    --bd-sidebar-link-bg: rgba(var(--bd-violet-rgb), .1);
    --bd-callout-link: 10,88,202;
    --bd-callout-code-color: #ab296a;
    --bd-pre-bg: var(--bs-tertiary-bg)
}

.navbar {
    border-bottom-color: $border_bottom_color_navbar;
    height: 65px;
    background-image: linear-gradient(rgba(var(--bd-violet-rgb), 1), rgba(var(--bd-violet-rgb), 0.95));
    box-shadow: 0 0.5rem 1rem rgba(0,0,0,0.15), inset 0 -1px 0 rgba(255,255,255,0.15);
    border: $border_bottom_color_navbar;
    &-nav {
        margin-top: 6px !important;
    }
    img {
        width: 2.4rem;
        height: 2.4rem;
    }
    .logotype_navbar {
        svg {
            width: 50px;
            height: 50px;
        }
        &__bg {
            fill: $bg_logotype_nabvar;
        }
    }
    &-brand {
        margin-top: -4px;
        h1 {
            font-family: $font_secondary;
            font-size: 1.5rem;
            color: $color_links_navbar;
            margin-left: 10px;
            &:hover {
                opacity: .85;
            }
        }
    }
    .nav-link {
        font-size: 1.1rem;
        font-family: $font_secondary;
        color: $color_links_navbar;
        -moz-transition: all .2s ease-in;
        -o-transition: all .2s ease-in;
        -webkit-transition: all .2s ease-in;
        transition: all .2s ease-in;
        &:hover {
            opacity: 0.85;
        }
        .fa-magnifying-glass {
            font-size: 1rem;
        }
    }
    // .nav-link::after {
    //     content: '|';
    //     padding: 0rem;
    // }

    // .navbar-nav .nav-item + .nav-item:before {
