.bottomAnimation {
    color: #666;
    display:inline-block;
    margin:0;
}
.bottomAnimation:after {
  display:block;
  content: '';
  border-bottom: solid 3px $bottom_animation_color;
  transform: scaleX(0);
  transition: transform 250ms ease-in-out;
}
.bottomAnimation:hover:after { transform: scaleX(1); }
.bottomAnimation.fromRight:after{ transform-origin:100% 50%; }
.bottomAnimation.fromLeft:after{  transform-origin:  0% 50%; }


.btn_download {
  text-align: center;
  margin: 40px 0px;
  a {
    text-decoration: none !important;
    background-color: $bg_btn_download;
    color: $color_btn_download;
    padding: 15px;
    min-width: 200px !important;
    border-radius: 5px;
    -moz-transition: all .2s ease-in;
    -o-transition: all .2s ease-in;
    -webkit-transition: all .2s ease-in;
    transition: all .2s ease-in;
    font-size: 2rem;
    border: 1px solid $color_border_btn_download;
    &:hover,&:focus,&:active {
      outline: none;
      text-decoration: none;
      opacity: .85;
    }
  }
}

.video {
  margin: 50px 0px;
  border: 5px solid $border_color_video;
}