.sidebar{
    font-family: $font_primary;
    color: $color_text_sidebar;
    .bottomAnimation:after {
        border-bottom: solid 3px #712cf9 !important;
    }
    .post-img {
        img {
            border: 8px solid $color_border_img_sidebar;
            border-radius: 5px;
            box-shadow: 0px 0px 30px $color_cards_decoration_1;
        }
    }
    &__recent {
        border: 1px solid transparent !important;
        h2 {
            font-family: $font_secondary;
            font-size: 2.5rem;
        }
        a {
            font-family: $font_secondary;
            color: $color_links_sidebar;
            font-size: 1.5rem;
            &:hover {
                opacity: 0.85;
            }
        }
        a:before {
            content: '>';
            margin-right: 5px;
        }
    }
}