.layout-search {
    &__input{
        font-size: 1.2rem;
        background-color: transparent;
        width: 100%;
        box-shadow: none;
        border: none;
        border-bottom: 1px solid $color_border_input_search;
        outline: none;
        border-radius: 0px;
        height: auto;
        margin-top: 3rem;
        font-size: 25px;
        font-family: $font_primary;
        border-bottom: 1px solid $color_bottom_input_search;
        margin-left: -50px;

        &:hover,&:focus,&:active,&:visited{
          background-color: transparent;
          box-shadow: none;
          outline: none;
        }
        &:focus, &:active, &:visited{
          border-bottom: 1px solid $color_bottom_input_search_hover;
        }
        @media (min-width: 300px) and (max-width: 992px) {
          font-size: 1.2rem !important;
        }
      }

      &__data__not-search {
        font-family: $font_secondary;
        font-size: 2.5rem;
        color: red;
      }

      &__arrow {
        font-size: 3rem;
        margin-top: 2rem;
        font-family: $font_secondary;
        color: $color_bottom_input_search;
      }

      // &__logotype{
      //   width: 150px;
      //   height: 150px;
      //   text-align: center;
      // }

}