.card {
    background-color: $bg_cards;
    box-shadow: 5px 5px 10px $color_cards_decoration_1;
    min-height: 520px;
    border: 2px solid $border_color_cards;
    border-radius: 0px;
    &:hover,
    &:focus,
    &:active {
      box-shadow: 0px 1px 0px $color_cards_decoration_0, 0px 2px 0px $color_cards_decoration_0, 0px 3px 0px $color_cards_decoration_0,
        0px 4px 0px $color_cards_decoration_0, 0px 6px 0px $color_cards_decoration_2,
        0px 1px 0px $color_cards_decoration_3, 0px 3px 0px $color_cards_decoration_4,
        0px 5px 0px $color_cards_decoration_5, 0 7px 7px $color_cards_decoration_6,
        0 8px 7px $color_cards_decoration_7, 0 9px 9px $color_cards_decoration_8,
        0 10px 10px $color_cards_decoration_9, 0 15px 15px $color_cards_decoration_10;
    }
}

.card-body {
    background-color: $bg_cards;
    color: $color_text_cards !important;
    font-family: $font_primary;
    h2.panel-title{
        font-family: $font_secondary;
        color: $color_title_cards;
        font-size: 1.9rem;
    }
    &__excerpt {
        color: $color_text_cards;
    }
}

.pagination {
    font-family: $font_secondary;
    margin: 0 1rem;
    font-size: 1.6rem;
    &--previous, &--next {
        color: #2828ff;
        outline: none;
        &:hover, &:focus, &:active {
            color: #2828ff;
            outline: none;
            text-decoration: none;
        }
    }
}

.disabled {
    color: $color_btn_disabled;
    &:hover {
        color: $color_btn_disabled;
    }
}