.post-layout{
  padding-top: 40px;
    color: $color_post_text;
    .post-title{
        font-family: $font_secondary;
        color: $color_post_title;
        font-size: 4rem;
        text-decoration: none;
        line-height: normal;
    }
    .post-title::before {
        content: '> ';
    }
    .post-meta{

        time {
            color: $color_post_meta;
            padding: 5px;
            background-color: $bg_post_meta;
            border-radius: 5px;
        }

    }
    .post-content{
        font-family: $font_primary;
        font-size: 20px;
        a {
            text-decoration: underline;
        }
        .highlight {
          border-radius: 10px;
            background-color: $bg_post_highlight;
            color: white;
            pre {
                padding: 20px 10px;
            }
        }
        &__pre {
            background-color: $bg_post_highlight;
            padding: 15px;
        }

        h1,h2 {
            margin: 30px 0px;
        }
        details  {
          padding: 10px;
          background-color: $bg_post_details;
          border-radius: 5px;
          summary {
            padding: 10px;
            background-color: $bg_post_summary;
            color: $color_post_summary;
          }
        }
        blockquote {
            border: none !important;
            background-color: $bg_post_blockquote;
            border-left: 10px solid $bf_post_blockquote;
            padding: 10px 10px 1px 10px;
            border-top-left-radius: 0px;
            border-bottom-left-radius: 0px;
            border-bottom-right-radius: 0px;
            border-radius: 5px;
        }

        table{
            margin: 2% 0%;
            width: 100%;
            border-radius: 5px;
            thead{
              tr{
                th{
                  padding: 5px;
                  border: 1px solid $color_post_table;
                  color: $color_post_table;
                  background-color: $bg_post_table;
                  border-radius: 6px;
                }
              }
            }
            tbody{
              tr{
                td{
                  border-radius: 6px;
                  padding: 5px;
                  border: 1px solid $color_post_table;
                  color: $color_post_table;
                  background-color: $bg_post_table;
                  code{
                    background-color: transparent;
                  }
                  a {
                    color: $color_link_table;
                  }
                }
              }
            }
          }
    }
}