.page-layout {
    padding-top: 40px;
    .post-title{
        font-family: $font_secondary;
        color: $color_page_title;
    }
    .post-title::before {
        content: '>';
        margin-right: 8px;
    }
    .post-meta{
        time {
            color: $color_page_meta;
            padding: 5px;
            background-color: $bg_page_meta;
        }

    }
    .post-content {
        text-align: center;
        font-family: $font_primary;
        font-size: 20px;
        a {
            text-decoration: underline;
        }
        .highlight {
            background-color: $bg_page_highlight;
        }
    }

    .about_logotype{
        text-align: center;
        width: 400px;
        height: 400px;
        margin-bottom: 50px;
        img {
            width: 400px;
            height: 400px;
        }
        &__bg {
            fill: $color_about_logotype;
        }
    }
}